import React, { Component } from "react";
import axios from "axios";
import MeetupsItem from "./MeetupItem";

class Meetups extends Component {
  constructor() {
    super();
    this.state = {
      meetups: []
    };
  }

  componentWillMount() {
    this.getMeetups();
  }

  getMeetups() {
    axios
      .get("http://localhost:3001/api/meetups")
      .then(response => {
        this.setState({ meetups: response.data }, () => {});
      })
      .catch(error => {
        console.error(error);
      });
  }

  render() {
    const meetupsItems = this.state.meetups.map((meetup, i) => {
      return <MeetupsItem key={meetup.id} item={meetup} />;
    });
    return (
      <div>
        <h1>Meetups</h1>
        <ul className="collection">{meetupsItems}</ul>
      </div>
    );
  }
}

export default Meetups;
